.main {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    margin: 0;
    padding: auto;
}

.container {
    width: 80vw;
    height: 80vh;
    margin: 0 auto;
    margin-top: 40px;
    display: flex;
    align-items: center;
    visibility: hidden;
}

.img-cont {
    width: 100vw;
    object-fit: cover;
    position: relative;
    overflow: hidden;
    margin-top: 60px;
}

.img-cont:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    background-color: white
}

.img-cont img {
    width: 100%;
    /* position: absolute; */
}

.text {
    position: relative;
    font-family: Poppins, "Helvetica Neue", sans-serif;
}

.text p {
    font-family: Poppins, "Helvetica Neue", sans-serif;
    font-size: 3vw;
    font-style: italic;
    font-weight: 600;
    cursor: pointer;
}

@media(max-width:900px) {
    .container {
        display: block;
    }
    .img-cont img {
        width: 80%;
    }
    .text p {
        font-size: 6vw;
    }
}